import React, { Component } from "react";
import { Link } from "react-router-dom";
import "../styles/Home.css";

export default class Home extends Component {
    render() {
        return (
            <div className="Home">
                <div className="lander">
                    <h1>Pgina de Inscries</h1>
                    <h3><p>16 Edio: incio a 22 de Setembro de 2023</p></h3>
                    <h4><p style={{ color: 'red' }}>Prazo limite de inscries: 10 de Setembro de 2023</p></h4>
                </div>     
<p>Para participar no torneio, as coletividades interessadas tero de inscrever os escales e respectivos jogadores e equipa tcnica 
neste site.<br />
A partir da 13 edio (2018) as inscries devem ser feitas exclusivamente atravs deste site. 
Mantm-se os requisitos das edies anteriores:  necessrio uma <strong>fotografia</strong> e a <strong>folha de inscrio</strong>
&nbsp;(disponvel na rea de <Link to="/documents">documentos</Link>) com os dados do jogador e assinatura (para jogadores menores 
de 18 anos a assinatura do encarregado de educao).
A fotografia pode ser tirada com um telemvel (deve ser idntica a uma fotografia tipo passe) e a folha de inscrio deve ser preenchida 
e posteriormente digitalizada, sendo ambos carregados na pgina de inscrio de jogador. Para a equipa tcnica apenas  necessria a fotografia.</p>

<p>Os escales disponveis para inscrio so os seguintes:</p>
<ul>
    <li><strong>Escolinhas</strong> - masculino e feminino, para nascidos entre 19/11/2014 e 31/12/2018 (Dos 5 aos 8 anos);</li>
    <li><strong>I Escalo</strong> - masculino e feminino, para nascidos entre 19/11/2010 e 18/11/2014 (Dos 8 aos 12 anos);</li>
    <li><strong>II Escalo</strong> - masculino para nascidos entre 19-11-2005 a 18-11-2010 (Dos 13 aos 17 anos);</li>
    <li><strong>III Escalo</strong> - masculino para nascidos antes de 19-11-2005 (+ de 17 anos);</li>
    <li><strong>Escalo Feminino</strong> - para nascidas antes de 19-11-2010 (+ de 12 anos).</li>
</ul>

<p>Para mais informaes sobre o torneio consulte o 
    <a href="https://drive.google.com/file/d/11Shg03w-2W7DxK663heO7nryndqr6Jy6/view?usp=sharing" target="_blank" rel="noopener noreferrer"> Regulamento Desportivo</a>.</p>
<p>Qualquer problema ou questo relacionada com a pgina de inscries, envie um mail para <a href="mailto:courela+tacabarnabe@gmail.com">courela+tacabarnabe@gmail.com</a>.</p>

<p>Acompanhe-nos no Facebook e no Twitter.</p>
                <h2>Links</h2>
                <ul>
                    <li><a href="https://www.facebook.com/tacabarnabe" target="_blank" rel="noopener noreferrer">Facebook</a></li>
                    <li><a href="https://twitter.com/tacabarnabe" target="_blank" rel="noopener noreferrer">Twitter</a></li>
                </ul>
            </div>
        );
    }
}
